.board-container {
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include for-tablet-layout {
        width: fit-content;
        margin-top: rem(59px);
        flex-direction: row;
        align-items: unset;
    }

    .add-group-toggle-btn {
        margin: 0;
        z-index: 0;
        min-width: rem(265px);
        color: white;
        background-color: rgba(0, 0, 0, 0.226);
        border-radius: rem(5px);
        height: rem(37px);
        margin: rem(70px) 0px rem(20px) 0px;

        @include for-tablet-layout {
            margin: unset;
        }

    }
    .add-group-toggle-btn:hover {
        background-color: rgba(0, 0, 0, 0.658);
    }
    .add-group-form {
        z-index: 5;
        background-color: #ebecf0;
        height: fit-content;
        border-radius: rem(5px);
        padding: rem(3px);
        width: rem(265px);
        margin-top: rem(67px);
        margin-bottom: rem(22px);

        @include for-tablet-layout {
            margin: unset;
        }

        .group-title-input {
            outline: none;
            border: none;
            border-radius: rem(3px);
            background-color: #fff;
            color: #00395aec;
            box-shadow: inset 0 0 0 rem(2px) #0079bf;
            margin: 0;
            width: 100%;
            padding: rem(8px) rem(12px);
        }
        .group-title-input::placeholder {
            opacity: 1;
            color: #00395aec;
        }
        .add-group-controls {
            height: rem(32px);
            margin: rem(4px) 0px 0px 0px;
            display: flex;
            .add-group-close-btn {
                padding: 0;
                margin: 0;
                width: rem(32px);
                font-size: 1.1rem;
                color: #42526ec5;
            }
            .add-group-close-btn:hover {
                color: #2e3542;
            }
            .add-group-add-btn {
                background-color: #0079bf;
                color: white;
                height: rem(32px);
                margin: 0px;
                border-radius: rem(3px);
            }
            .add-group-add-btn:hover {
                background-color: #0069a7;
            }
        }
    }
}
.board-window {
    box-shadow: inset 0 0 0 rem(2000px) rgba(255, 255, 255, 0.109);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100%;
    z-index: 0;
    position: fixed;
    top: 0;
    left: 0;
}
.task-details-container {
    display: flex;
    justify-content: center;
}
